import service from "../../service.js";
export default{
	data() {
		return {
			img_arr: [],
			array:'',
			indexs:0
		}
	},
	methods:{
		jump(){
			uni.navigateBack(1);
		},
		upload(){
			var self = this;
			uni.chooseImage({
			    success: (chooseImageRes) => {
			        const tempFilePaths = chooseImageRes.tempFilePaths;
			        uni.uploadFile({
			            url: service.api.user.updateIdImage, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
			            filePath: tempFilePaths[0],
			            name: 'file',
			            success: (res) => {
			                console.log(JSON.parse(res.data),"asfasdf");
							const path = JSON.parse(res.data);//打印出来就知道为什么要用JSON.parse
							// self.invoiceimg = path.data.url;
							// console.log(path)
							self.img_arr.push(path.data.url)
							console.log(self.img_arr)
							uni.showToast({
								title:path.info,
								icon:'none'
							});
			            }
			        });
			    }
			});
		},
		close(index){
			this.img_arr.splice(index,1)
			console.log(this.img_arr)
		},
		Submi(){
			if(this.img_arr == ''){
				uni.showToast({
					icon:'none',
					title:'请上传首刷凭证！'
				})
				return
			}
			let arr = []
			arr.push(this.img_arr)
			service.auth(this , service.api.service.cardCheck , 'POST' , {
				card_fields:arr,
				card_id:this.array[this.indexs].id,
				token:uni.getStorageSync('token')
			} , function(self ,res){
				console.log(res);
				uni.showToast({
					icon:'none',
					title:res.info
				})
				if(res.code == 1){
					setTimeout(function(){
						uni.navigateBack(1)
					},1000)
				}
			})
			
		},
		bindPickerChange(e){
			console.log(e)
			this.indexs = e.detail.value
		}
	},
	onLoad() {
		service.auth(this , service.api.service.cardList , 'GET' , {} , function(self ,res){
			console.log(res);
			self.array = res.data	
		})
		
	}
}
